import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    base_url: null,
    poster_size: null,
    thumb_size: null,
    logo_size: null,
    isLoading: false
  },
  mutations: {
    IMG_SETTINGS (state, payload) {
      const {
        base_url,
        poster_sizes,
        backdrop_sizes,
        logo_sizes
      } = payload.images
      state.base_url = base_url
      state.poster_size = backdrop_sizes[3]
      state.thumb_size = poster_sizes[3]
      state.logo_size = logo_sizes[1]
    },
    ON_LOADING (state) {
      state.isLoading = true
    },
    OFF_LOADING (state) {
      setTimeout(
        () => {
          state.isLoading = false
        },
        1200
      )
    }
  },
  actions: {
    imgSettings ({ commit }, payload) {
      commit('IMG_SETTINGS', payload)
    },
    onLoading ({ commit }) {
      commit('ON_LOADING')
    },
    offLoading ({ commit }) {
      commit('OFF_LOADING')
    }
  },
  getters: {
    base_url: state => state.base_url,
    isLoading: state => state.isLoading
  }
})
