import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import NotFound from "./views/Movie.vue"

Vue.use(Router);

export default new Router({
	mode: "history",
	scrollBehavior: () => ({ y: 0 }),
	base: process.env.BASE_URL,
	routes: [
		{
			path: '*',
			component: NotFound
		},
		{
			path: "/",
			name: "home",
			component: Home
		},
		{
			path: "/movie/:id",
			name: "movie",
			component: () => import("./views/Movie.vue"),
			props: true
		},
	]
});
