<template lang="pug">
section
	ul.popular.row.m-0
		li.col-12 Sort
		li.col-6.col-sm-6.col-md-4.col-lg-3.p-0(v-for="(item, index) in popular")
			popular(:item="item" :key="item.id")
	button(@click="nextPage") load more
</template>

<script>
import Popular from '@/components/Popular';

export default {
	name: "home",
	components: {
		Popular
	},
	data: () => ({
		configuration: {},
		current_page: 1,
		popular: [],
		total_pages: null,
		page: 1,
		lockScroll: false,
		size: 20
	}),
	methods: {
		scroll() {
			const self = this;
			window.addEventListener('scroll', () => {
				let bottomOfWindow = document.documentElement.scrollTop + window.innerHeight === document.documentElement.offsetHeight;
				if (bottomOfWindow) {
					self.nextPage();
				}
			})
		},
		getConfiguration() {
			this.$http.get('/configuration')
			.then(res => {
				this.$store.dispatch('imgSettings', res.data);
			})
			.catch(error => console.log(error))
		},
		getPopular() {
			this.$http.get('movie/popular')
				.then(res => {
					const {
						results,
						page,
						total_pages
					} = res.data;
					this.popular = results;
					this.page = page;
					this.total_pages = total_pages;
				}).then(() => {
					this.$store.dispatch('offLoading')
				})
				.catch(error => console.log(error))
		},
		nextPage() {
			const self = this;
			this.$http.get('movie/popular', {
					params: {
						page: ++self.current_page
					}
				})
				.then((res) => {
					this.lockScroll = true;
					return res;
				})
				.then((res) => {
					this.popular = [...this.popular, ...res.data.results];
				})
				.then(() => self.lockScroll = false)
				.catch((error) => console.log(error))
		}
	},
	created() {
		this.getConfiguration();
		this.getPopular();
	},
	mounted() {
		this.scroll()
	}

};

</script>

