<template lang="pug">
.search
  input(
    class=""
    v-model="inputText"
    @input="search"
    @keyup.enter.prevent.stop="submit"
    @keyup.up.prevent.stop="selectUp"
    @keyup.down.prevent.stop="selectDown"
    ref="customSearch"
    placeholder="Enter film name")
  transition(name="fade")
    ul.list
      li(
				v-for="(option, index) in options"
				@click="submit"
				:class="['item', { selected: (selected == index) ? 'selected' : ''}]") {{ option.title }}
</template>

<script>

export default {
  data: () => ({
    selected: 0,
    inputText: "",
    options: [],
    result: []
  }),
  methods: {
    submit() {
      this.$router.push({
        name: 'movie',
        path: '/movie/',
        params: {
          id: this.options[this.selected].id
        }
      });
			this.selected = 0;
			this.options = [];
			this.result = [];
			this.inputText = "";
    },

    search () {
      // add debounce
      this.$http.get("/search/movie", {
        params: {
          query: escape( this.inputText )
        }
      })
      .then(res => {
        this.options = res.data.results;
      })
      .catch(error => console.log(error))
    },

    selectUp(e) {
      if(this.selected === 0) return;
      else this.selected -= 1;
      this.showSelected();
    },

    selectDown() {
      if(this.selected === this.optionsLength - 1) return;
      this.selected += 1;
      this.showSelected();
    },

    showSelected() {
      this.inputText = this.options[this.selected].title
    }
  },
  computed: {
    optionsLength() {
      return this.options.length
    }
  }
}
</script>

