  max-width: 280px;
  display: flex;
  width: 100%;
  position: relative;
}
.list {
  padding: 0;
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
	background: #FFF;
}
.item {
  list-style: none;
	padding: .7em;
	overflow: hidden;
}
.selected {
  background-color: lightgray;
}
input {
  display: block;
  line-height: 40px;
