<template lang="pug">
transition(name="fade")
	.poster
		slot(name="title")
		template(v-if="src == undefined || src == null")
		template(v-else)
			img(class="poster__img" :src="base_url + img_size + src" @load="imgLoaded" v-show="showImg")
</template>

<script>
import {
	mapState,
	mapGetters
} from "vuex";

export default {
	props: ["src", "size"],
	data: () => ({
		showImg: false
	}),
	computed: {
		...mapGetters(["base_url"]),
		...mapState(["poster_size", "thumb_size", "logo_size"]),
		img_size() {
			let size = "";
			switch (this.size) {
				case "sm": size = this.thumb_size;
					break;
				case "xs": size = this.logo_size;
					break;
				default: size =  this.poster_size;
			}
			return size;
		}
	},
	methods: {
		imgLoaded: function() {
	    this.showImg = true;
	  }
	}
}
</script>

<style lang="scss">
.poster {
	position: relative;
	overflow: hidden;

	.title {
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		padding: 1em;
		//background-color: rgba(0, 0 ,0 , 0.9);
		background-color: rgba(45, 52, 54, .9);
		color: #FFF;
		transform: translateY(100%);
		transition: transform .3s  ease-out;
		border-top: 2px solid rgba(255, 255, 255, .9);
		box-shadow: 0 5px 5px -5px #000;
		font-family:"Helvetica Neue", Helvetica, Sans-serif;
	}

	&:hover {
		cursor: pointer;
		.title {
			z-index: 1;
			transform: translateY(0%);
			transition-delay: 0.1s;
		}

		.poster__img {
			transform: scale(1.1)
		}
	}
}
.poster__img {
	max-width: 100%;
	display: block;
	transition: transform .3s ease
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.3s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}

</style>
