<template lang="pug">
.details
	h2 {{ details.title }}
	//pre {{ details }}
	poster(:src="details.backdrop_path")
	div {{ details.overview }}	

	ul.production-list
		li.title
			b Production companies:
		li(v-for="item in details.production_companies")
			//poster(:src="item.logo_path" size="xs")
			.name {{ item.name }}
	hr

	.genres-list
		li
			b Genres:
		li(v-for="item in details.genres") {{ item.name }}

	hr

	similar
</template>

<script>
import Poster from '@/components/Poster';
import Similar from '@/components/Similar';

export default {
	props: ['id'],
	components: {
		Poster,
		Similar
	},
	watch: {
		"id"() {
			this.getDetails(this.id)
		}
	},
	data: () => ({
		details: {}
	}),
	methods: {
		getDetails(id) {
			this.$http.get(`movie/${id}`)
			.then(res => {
				this.details = res.data
			})
			// .then(() => {
			// 	this.$store.dispatch('offLoading')
			// })
			.catch(error => console.log(error))
		}
	},
	created() {
		this.getDetails(this.id)
	}
}
</script>

