<template lang="pug">
.movie
	movie-details(:id="id")
	//pre {{ details }}
</template>

<script>
import MovieDetails from '@/components/MovieDetails';

export default {
	props: ['id'],
	components: {
		MovieDetails
	},
	data: () => ({
		details: null
	}),
	methods: {
		getConfiguration() {
			this.$http.get('/configuration').then(res => {
				this.$store.dispatch('imgSettings', res.data);
			})
			.catch(error => console.log(error))
		},
	},
	created() {
		this.getConfiguration()
	}
}
</script>

