<template lang="pug">
.movie-similar(v-if="similar_list && similar_list.length > 0")
  carousel(:per-page="4" :mouse-drag="false")
    slide(v-for="(item, index) in similar_list")
      router-link(class="popular-item__title" :to="{ name: 'movie', path: '/movie/', params: { id: item.id } }")
        poster(:src="item.poster_path" size="sm")
        div {{ item.title }}
</template>

<script>
import Poster from '@/components/Poster';
import { Carousel, Slide } from 'vue-carousel';

export default {
  name: 'similar',
  props: ['id'],
  components: {
    Poster,
    Carousel,
    Slide
  },

  data: () => ({
    similar_list: null
  }),
  watch: {
    "$route"() {
      
    }
  },
  methods: {
    getSimilar(id) {
			this.$http.get(`movie/${id}/similar`)
			.then(res => {
				// const { results, page, total_pages } = res.data;
				// self.popular = results;
				// self.page = page;
				// self.total_pages = total_pages;)
				this.similar_list = res.data.results
			})
			.catch(error => console.log(error))
		},
  },

  mounted() {
		this.getSimilar(this.id);
	}
}
</script>

